//
// Responsive: Utility classes for special devices
// --------------------------------------------------
//
// These are visibility utilities for special devices to
// allow for more flexibility in building specific layouts
// apart from the usual screen sizes xs, sm, md, lg,
// especially for smaller devices...

// iPhones (portrait mode):
@screen-i4:   320;
@screen-i5:   @screen-i4;
@screen-i6:   375;
@screen-i6P:  414;

// iPhones (landscape mode):
@screen-i4L:   480;
@screen-i5L:   568;
@screen-i6L:   667;
@screen-i6PL:  736;

.initDevicesVisibilityClasses(@device) {
  .visible-gt-@{device},
  .visible-le-@{device} {
    .responsive-invisibility();
  }
  .visible-gt-@{device}-block,
  .visible-le-@{device}-block,
  .visible-gt-@{device}-inline,
  .visible-le-@{device}-inline,
  .visible-gt-@{device}-inline-block,
  .visible-le-@{device}-inline-block {
    display: none !important;
  }
}

.initDevicesVisibilityClasses(i4);
.initDevicesVisibilityClasses(i4L);
.initDevicesVisibilityClasses(i5);
.initDevicesVisibilityClasses(i5L);
.initDevicesVisibilityClasses(i6);
.initDevicesVisibilityClasses(i6L);
.initDevicesVisibilityClasses(i6P);
.initDevicesVisibilityClasses(i6PL);

//

.makeDevicesDeprecatedVisibilityClasses(@device, @i, @unit: px) {
  .visible-gt-@{device} {
    @media (min-width: unit((@i + 1), @unit)) {
      .responsive-visibility();
    }
  }
  .visible-le-@{device} {
    @media (max-width: unit(@i, @unit)) {
      .responsive-visibility();
    }
  }
}

.makeDevicesDeprecatedVisibilityClasses(i4,   @screen-i4);
.makeDevicesDeprecatedVisibilityClasses(i4L,  @screen-i4L);
.makeDevicesDeprecatedVisibilityClasses(i5,   @screen-i5);
.makeDevicesDeprecatedVisibilityClasses(i5L,  @screen-i5L);
.makeDevicesDeprecatedVisibilityClasses(i6,   @screen-i6);
.makeDevicesDeprecatedVisibilityClasses(i6L,  @screen-i6L);
.makeDevicesDeprecatedVisibilityClasses(i6P,  @screen-i6P);
.makeDevicesDeprecatedVisibilityClasses(i6PL, @screen-i6PL);

//

.makeDevicesDisplayPropertyVisibilityClasses(@device, @i, @display-property: inline-block, @unit: px) {
  .visible-gt-@{device}-@{display-property} {
    @media (min-width: unit((@i + 1), @unit)) {
      display: @display-property !important;
    }
  }
  .visible-le-@{device}-@{display-property} {
    @media (max-width: unit(@i, @unit)) {
      display: @display-property !important;
    }
  }
}

.makeDevicesDisplayPropertyVisibilityClasses(i4,   @screen-i4,   block);
.makeDevicesDisplayPropertyVisibilityClasses(i4L,  @screen-i4L,  block);
.makeDevicesDisplayPropertyVisibilityClasses(i5,   @screen-i5,   block);
.makeDevicesDisplayPropertyVisibilityClasses(i5L,  @screen-i5L,  block);
.makeDevicesDisplayPropertyVisibilityClasses(i6,   @screen-i6,   block);
.makeDevicesDisplayPropertyVisibilityClasses(i6L,  @screen-i6L,  block);
.makeDevicesDisplayPropertyVisibilityClasses(i6P,  @screen-i6P,  block);
.makeDevicesDisplayPropertyVisibilityClasses(i6PL, @screen-i6PL, block);

.makeDevicesDisplayPropertyVisibilityClasses(i4,   @screen-i4,   inline);
.makeDevicesDisplayPropertyVisibilityClasses(i4L,  @screen-i4L,  inline);
.makeDevicesDisplayPropertyVisibilityClasses(i5,   @screen-i5,   inline);
.makeDevicesDisplayPropertyVisibilityClasses(i5L,  @screen-i5L,  inline);
.makeDevicesDisplayPropertyVisibilityClasses(i6,   @screen-i6,   inline);
.makeDevicesDisplayPropertyVisibilityClasses(i6L,  @screen-i6L,  inline);
.makeDevicesDisplayPropertyVisibilityClasses(i6P,  @screen-i6P,  inline);
.makeDevicesDisplayPropertyVisibilityClasses(i6PL, @screen-i6PL, inline);

.makeDevicesDisplayPropertyVisibilityClasses(i4,   @screen-i4,   inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i4L,  @screen-i4L,  inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i5,   @screen-i5,   inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i5L,  @screen-i5L,  inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i6,   @screen-i6,   inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i6L,  @screen-i6L,  inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i6P,  @screen-i6P,  inline-block);
.makeDevicesDisplayPropertyVisibilityClasses(i6PL, @screen-i6PL, inline-block);

//

.makeDevicesHiddenClasses(@device, @i, @unit: px) {
  .hidden-gt-@{device} {
    @media (min-width: unit((@i + 1), @unit)) {
      .responsive-invisibility();
    }
  }
  .hidden-le-@{device} {
    @media (max-width: unit(@i, @unit)) {
      .responsive-invisibility();
    }
  }
}

.makeDevicesHiddenClasses(i4,   @screen-i4);
.makeDevicesHiddenClasses(i4L,  @screen-i4L);
.makeDevicesHiddenClasses(i5,   @screen-i5);
.makeDevicesHiddenClasses(i5L,  @screen-i5L);
.makeDevicesHiddenClasses(i6,   @screen-i6);
.makeDevicesHiddenClasses(i6L,  @screen-i6L);
.makeDevicesHiddenClasses(i6P,  @screen-i6P);
.makeDevicesHiddenClasses(i6PL, @screen-i6PL);
