//
// Responsive: Utility classes for flexible XS screen designs
// ----------------------------------------------------------
//

.initVisibilityClasses(@max, @i:100) when (@i <= @max) {
  .visible-gt-@{i},
  .visible-le-@{i} {
    .responsive-invisibility();
  }
  .visible-gt-@{i}-block,
  .visible-le-@{i}-block,
  .visible-gt-@{i}-inline,
  .visible-le-@{i}-inline,
  .visible-gt-@{i}-inline-block,
  .visible-le-@{i}-inline-block {
    display: none !important;
  }
  .initVisibilityClasses(@max, @i:(@i+10));
}

.initVisibilityClasses(800);

//

.makeDeprecatedVisibilityClasses(@max, @unit: px, @i:100) when (@i <= @max) {
  .visible-gt-@{i} {
     @media (min-width: unit((@i + 1), @unit)) {
        .responsive-visibility();
      }
  }
  .visible-le-@{i} {
      @media (max-width: unit(@i, @unit)) {
        .responsive-visibility();
      }
  }
  .makeDeprecatedVisibilityClasses(@max,  @i:(@i+10));
}

.makeDeprecatedVisibilityClasses(800);

//

.makeDisplayPropertyVisibilityClasses(@max, @display-property: inline-block, @unit: px, @i:100) when (@i <= @max) {
  .visible-gt-@{i}-@{display-property} {
      @media (min-width: unit((@i + 1), @unit)) {
          display: @display-property !important;
      }
  }
  .visible-le-@{i}-@{display-property} {
      @media (max-width: unit(@i, @unit)) {
          display: @display-property !important;
      }
  }
  .makeDisplayPropertyVisibilityClasses(@max, @display-property, @i:(@i+10));
}

.makeDisplayPropertyVisibilityClasses(800, block);
.makeDisplayPropertyVisibilityClasses(800, inline);
.makeDisplayPropertyVisibilityClasses(800, inline-block);

//

.makeHiddenClasses(@max, @unit: px, @prop: width,  @i:10) when (@i <= @max) {
  .hidden-gt-@{i} {
      @media (min-width: unit((@i + 1), @unit)) {
        .responsive-invisibility();
      }
  }
  .hidden-le-@{i} {
      @media (max-width: unit(@i, @unit)) {
        .responsive-invisibility();
      }
  }
  .makeHiddenClasses(@max,  @i:(@i+10));
}

.makeHiddenClasses(800);
